import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {MaterialLComponent} from './pages/material-l/material-l.component';
import {MaterialGComponent} from './pages/material-g/material-g.component';
import {MaterialComponent} from './pages/material/material.component';
import {PrimeComponent} from './pages/prime/prime.component';

const routes: Routes = [
  {component: MaterialComponent, path: ""},
  {component: PrimeComponent, path: "prime"},
  {component: MaterialLComponent, path: "material-l"},
  {component: MaterialGComponent, path: "material-g"},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
